* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;
}

a {
  color: white;
  transition: all 0.5s ease-in-out;
}

a:link,a:visited,a:active {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  font-size: 2rem;
  text-shadow: 0 0.2rem 0.2rem rgba(0, 0, 0, 0.5);
}

.footer a {
  color: #54585a;
}

html {
 /* 1rem = 10px */
  font-size: 62.5%;
}/* HEADER */.header {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: rgba(9, 157, 142, 0.9);
  color: white;
  font-size: 1.5rem;
  line-height: 3rem;
  font-family: rubik;
  align-items: center;
  position: fixed;
  z-index: 1;
  opacity: 0.9;
  font-weight: 500;
}

.header__team__name {
  margin: 1.5rem;
  text-transform: capitalize;
  font-size: 2.4rem;
}

.header__team__name__yes {
  text-transform: uppercase;
  display: inline-block;
  animation-name: jump;
  animation-duration: 4s;
  animation-iteration-count: 5;
}

.header__menu {
  background-color: rgba(20, 217, 197, 0.9);
  width: 100%;
}

.header__menu__options {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.nav__team,.nav__who,.nav__contact {
  color: white;
  padding: 1rem;
  font-weight: 500;
  text-transform: uppercase;
  font-size: 1.6rem;
}

.nav__who {
  min-width: 14rem;
}/* HERO */.hero {
  background-image: url(../images/hero_768px.jpg);
  background-size: cover;
  background-position: left;
  width: 100%;
  height: 55rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.hero__claim {
  font-family: OpenSans-Semibold;
  font-size: 1.8rem;
  line-height: 2.5rem;
  text-align: center;
  background-color: white;
  width: 80%;
  transform: translateY(50%);
  padding: 1rem;
  font-weight: 600;
}/*  TEAM */.team {
  width: 100%;
  margin-top: 4rem;
  padding: 3rem 1.5rem 3rem 1.5rem;
}

.team__h2 {
  font-family: "Rubik-Medium", sans-serif;
  font-size: 2.4rem;
  text-align: center;
  color: #099d8d;
  height: 4.5rem;
  align-self: center;
  text-transform: uppercase;
}

.team__description {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  line-height: 2.4rem;
  color: #54585a;
  align-self: center;
}

.anchor__helper {
  display: block;
  position: relative;
  top: -12rem;
  visibility: hidden;
}/* DECORATION */.decoration {
  height: 3rem;
  width: 3rem;
  background: white;
  transform: rotate(45deg) translate(30%, 30%);
}

.decoration__container {
  width: 100%;
  display: flex;
  justify-content: center;
}/* QUALITIES S&F */.qualities__container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: #e8e2e1;
  width: 100%;
  padding: 0rem 1.5rem 4.5rem 1.5rem;
}

.qualities__title {
  font-family: "Rubik-Medium", sans-serif;
  font-size: 2.2rem;
  font-weight: 50rem;
  text-align: center;
  color: #099d8d;
  height: 4.5rem;
  margin-top: 3rem;
}

.qualities__description {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  line-height: 2.4rem;
  color: #54585a;
}/* ABOUT US */.individual__description {
  padding: 3rem 1.5rem 0rem 1.5rem;
}

.individual__description__title {
  font-family: "Rubik-Medium", sans-serif;
  color: #099d8d;
  line-height: 4.5rem;
  text-align: center;
  text-transform: uppercase;
  padding-bottom: 3rem;
  font-size: 2.4rem;
}

.individual__title {
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 2rem;
  line-height: 4.5rem;
}

.individual__image__adalaber {
  max-height: 18rem;
  border: solid 0.5rem;
  border-color: rgba(0, 0, 0, 0.08);
  margin: 0rem;
}

.individual__bio {
  font-family: "Open Sans", sans-serif;
  line-height: 2.4rem;
  color: #54585a;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.individual__text {
  text-align: center;
  font-size: 1.6rem;
}

.individual__logos {
  color: #099d8d;
  padding-top: 1.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 12.4rem;
}

.individual__logos__list:not(:first-child) {
  list-style-type: disc;
  color: black;
  align-self: center;
  font-size: 0.8rem;
}

.individual__logos__link {
  font-size: 1.5rem;
}

.anchor__who {
  display: block;
  position: relative;
  top: -11rem;
  visibility: hidden;
}

.adalabers {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0rem 0rem 4.5rem 0rem;
}

.fab,.fas {
  color: #099d8d;
}/* CONTACT */.contact__hero {
  background-image: url(../images/background__contact_320ult.jpg);
  background-size: cover;
  width: 100%;
  height: 300px;
}

.contact__info {
  padding: 15px 15px 45px 15px;
  font-family: "Open Sans", sans-serif;
  line-height: 24px;
  font-size: 16px;
  color: #54585a;
}

.contact__info__title {
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #099d8d;
  font-family: "Rubik-Medium", sans-serif;
  line-height: 45px;
  margin-top: 30px;
}

.contact__info__text {
  text-align: center;
  margin: 0 auto;
  width: 90%;
}

.contact__info--small {
  text-align: center;
  padding-top: 20px;
  font-size: 12px;
}

.form {
  display: flex;
  flex-direction: column;
  padding: 2px 15px 60px 15px;
  font-family: "Open Sans", sans-serif;
}

.form__items {
  display: flex;
  flex-direction: column;
}

.form__items__label {
  margin: 13px 0px 7px 0px;
  font-size: 14px;
  color: #54585a;
}

.form__items__phone {
  padding-top: 13px;
}

.form__items__email {
  padding-top: 13px;
}

.form__items__box {
  height: 45px;
  padding-left: 15px;
  font-size: 14px;
  border: solid 1px #b8b8b9;
  width: 100%;
  font-family: "Open Sans", sans-serif;
}

.form__items__box--email {
  width: 100%;
  height: 45px;
  border: solid 1px #b8b8b9;
  padding-left: 15px;
  margin-top: 7px;
  font-size: 16px;
}

.form__items__box--phone {
  width: 100%;
  height: 45px;
  border: solid 1px #b8b8b9;
  padding-left: 15px;
  margin-top: 7px;
  font-size: 14px;
}

.form__items__comment {
  padding-left: 15px;
  padding-top: 11px;
  border: solid 1px #b8b8b9;
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
}

.form__items__button {
  margin-top: 30px;
  height: 45px;
  border-radius: 4px;
  background-color: #099d8d;
  color: white;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 16px;
  transition: all ease 1s;
}

.form__items__button:hover {
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4);
  background-color: #1c756c;
}

.form__asterisc {
  color: #099d8d;
}/* FOOTER  */.footer {
  padding: 3rem 0rem 4.5rem 0rem;
  display: grid;
  border: 1px solid #b8b8b9;
  grid-template-columns: 1fr;
  grid-template-rows: 2rem, 6rem, 4.5rem;
  font-size: 1.4rem;
}

.footer__copyright {
  font-family: "Open Sans", sans-serif;
  color: #b8b8b9;
  line-height: 2rem;
  text-align: center;
}

.footer__menu {
  font-family: "Open Sans", sans-serif;
  text-align: center;
  line-height: 2rem;
  color: #54585a;
  font-weight: 600;
  opacity: 0.75;
  padding: 2rem 0rem 3rem 0rem;
  text-transform: uppercase;
}

.footer__logo {
  margin: 0rem auto;
}

@media all and (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    padding: 0rem 3rem 0rem 3rem;
  }

  .header__team__name {
    min-width: 35%;
  }

  .header__menu {
    background-color: transparent;
    width: 50%;
  }

  .header__menu__options {
    justify-content: flex-end;
  }

  .hero {
    background-image: url(../images/hero_1200px.jpg);
    align-items: flex-start;
  }

  .hero__claim {
    transform: translateY(0%);
    margin-left: 5%;
    max-width: 50rem;
    opacity: 0.8;
    font-size: 1.8rem;
  }

  .anchor__helper {
    display: block;
    position: relative;
    top: -8rem;
    visibility: hidden;
  }

  .team {
    margin-top: 0px;
    padding: 4.5rem 4.5rem 3rem 4.5rem;
  }

  .qualities__container {
    flex-direction: row;
    justify-content: space-between;
    padding: 4.5rem 4.5rem 6rem 4.5rem;
  }

  .qualities--strengths,.qualities--weaknesses {
    width: 50%;
    padding: 0rem 1.5rem;
  }

  .qualities__title {
    margin-top: 0rem;
  }

  .qualities__description {
    text-align: center;
  }

  .adalabers {
    flex-direction: row;
    min-width: 400px;
    padding: 3rem 0rem;
  }

  __image__adalaber {
    max-height: 18rem;
    box-sizing: border-box;
    border: solid 5rem;
    border-color: rgba(0, 0, 0, 0.08);
    margin: 0;
  }

  __bio {
    font-family: "Open Sans", sans-serif;
    line-height: 2.4rem;
    color: #54585a;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  __text {
    text-align: center;
  }

  .individual__description {
    padding: 4.5rem 4.5rem 3rem 4.5rem;
  }

  .individual__bio {
    align-items: flex-start;
    padding-left: 3rem;
  }

  .individual__text {
    text-align: left;
  }

  .individual__description__title {
    padding-bottom: 1.5rem;
  }

  .individual__image__adalaber {
    max-height: 20.5rem;
  }

  .adalabers:nth-child(even) {
    flex-direction: row-reverse;
    align-items: center;
    min-width: 40rem;
  }

  .adalabers:nth-child(even) .individual__bio {
    align-items: flex-end;
    padding: 0rem 3rem 0rem 0rem;
  }

  .adalabers:nth-child(even) .individual__bio .individual__text {
    text-align: right;
  }

  .anchor__who {
    top: -7rem;
  }

  .contact__hero {
    background-image: url(../images/background__contact_320.jpg);
    background-size: cover;
    width: 100%;
    height: 300px;
  }

  .form__container {
    padding: 0px 148px;
  }

  .form__items__button {
    width: 206px;
    align-self: flex-end;
  }

  .footer {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2rem, 9rem, 4.5rem;
    padding: 3rem 4.5rem 6rem 4.5rem;
  }

  .footer__menu {
    padding: 0rem;
    grid-row: 1/3;
  }

  .footer__copyright {
    grid-row: 1/2;
    text-align: left;
  }

  .footer__menu__list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    line-height: 3rem;
    margin: 0rem;
  }

  .footer__logo {
    width: 11.6rem;
    margin: 0rem;
  }
}

@media all and (min-width: 1200px) {
  .nav__team {
    font-size: 1.5rem;
  }

  .hero__claim {
    font-size: 2.4rem;
  }

  .team {
    padding: 4.5rem 0rem 3rem 0rem;
  }

  .team__description {
    max-width: 60.1rem;
    margin: 0rem auto;
  }

  .qualities__container {
    padding: 4.5rem 13.8rem 6rem 13.8rem;
  }

  .qualities--strengths,.qualities--weaknesses {
    padding: 0rem 2.3rem;
  }

  .individual__description {
    padding: 4.5rem 13.8rem 3rem 13.8rem;
  }

  .individual__bio {
    align-items: flex-start;
    padding-left: 4.5rem;
  }

  .individual__text {
    max-width: 55rem;
  }

  .individual__image__adalaber {
    max-height: 27.8rem;
  }

  .adalabers:nth-child(even) .individual__bio {
    padding: 0rem 4.5rem 0rem 0rem;
  }

  .contact__hero {
    background-image: url(../images/background__contact_1200.jpg);
    background-size: cover;
    width: 100%;
    height: 400px;
  }

  .form__container {
    padding: 0px 285px;
  }

  .form__items {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .form__items__email {
    width: 395px;
  }

  .form__items__phone {
    width: 162px;
  }

  .form__items__button {
    width: 278px;
    margin-bottom: 60px;
  }

  .form__items__direction {
    display: flex;
    flex-direction: column;
    flex-grow: 2;
  }

  .footer {
    padding: 3rem 13.8rem 4.7rem 13.8rem;
  }
}

@keyframes jump {
  0% {
    transform: scale(2);
  }

  50% {
    transform: rotateY(1360deg);
  }

  100% {
    transform: translateY(0);
  }
}
